import React from 'react';
import GoogleMapReact from 'google-map-react';
import styled from 'styled-components';
// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
// import { faMapMarkerAlt } from '@fortawesome/free-solid-svg-icons';

const GoogleMapContainer = styled.div`
  height: ${props => props.height + 'px'};
  width: 100%;
`;

const API_KEY = 'AIzaSyB1WIt5KPCACtHZ1X8KBFocllX_5zq4GL8';

const GoogleMapComponent = ({ height, isRenderPolygon, mapState }) => {
  const renderArea = (map, maps) => {
    let markers = [
      { lat: 41.89001042, lng: 23.0960083 },
      { lat: 42.61374895, lng: 22.73345947 },
      { lat: 43.39307372, lng: 22.87628174 },
      { lat: 44.02442152, lng: 22.86529541 },
      { lat: 43.28920196, lng: 23.98040771 },
      { lat: 42.25291778, lng: 23.23059082 },
      { lat: 41.89001042, lng: 23.0960083 },

    ];
    let geodesicPolyline = new maps.Polygon({
      path: markers,
      strokeColor: '#60feb2',
      strokeOpacity: 0.5,
      strokeWeight: 2,
      fillColor: '#158fce',
      fillOpacity: 0.35,
    })
    geodesicPolyline.setMap(map)
  }

  return (
    <>
      <GoogleMapContainer height={height}>
        <GoogleMapReact
          bootstrapURLKeys={{ key: API_KEY }}
          defaultCenter={mapState.center}
          defaultZoom={mapState.zoom}
          onGoogleApiLoaded={({ map, maps }) => {
            if (isRenderPolygon) {
              renderArea(map, maps);
            }
          }}
        >
        </GoogleMapReact>
      </GoogleMapContainer>
    </>
  );
}

export default GoogleMapComponent;